<template>
  <div class="container-fluid Background"
  :style="{ backgroundImage: 'url(' + backgroundImg + ')' }"></div>
</template>

<script>
export default {
  props: ['backgroundImg']
}
</script>


<style scoped>
.Background {
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  position: fixed;
  height: calc(100vh - 80px);
  min-height: 100vh;
  /* height: 100%; */
}
</style>



