import Vue from 'vue'
import Router from 'vue-router'
import index from '@/components/routes/index'
import exploreTheWolf from '@/components/routes/explore-the-wolf'
import landscape from '@/components/routes/landscape'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },
    {
      path: '/explore-the-wolf',
      name: 'Explore the Wolf',
      component: exploreTheWolf
    },
    {
      path: '/landscape',
      name: 'Landscape',
      component: landscape
    }
  ]
})



