<template>
  <div>
    <app-audio :src="'./static/audio/' + content.landscape.audio"></app-audio>
    <app-background :backgroundImg="content.landscape.backgroundImg"></app-background>

    <div class="container" style="padding-top: 8em;">

      <div class="row">
        <div class="col">
          <toggle-button
          v-model="landscapeState"
          :sync="true"/>
        </div>
      </div>

      <div class="row">
        <div class="col-6"><img src="./../../../static/images/landscape-without-wolves.jpg" alt="" srcset=""></div>
        <div class="col-6">thing</div>
      </div>

    </div>

  </div>
</template>

<script>
import appAudio from './../partials/audio'
import appBackground from './../partials/background'
import content from './../../../static/data/content.json'
export default {
  components: {
    appAudio,
    appBackground
  },

  data () {
    return {
      content,
      landscapeState: false
    }
  }
}
</script>


