<template>
  <nav class="container-fluid Navbar">

    <!-- mobile menu -->
    <transition name="slideIn">
      <div class="Navbar-menu Navbar-menu--mobile d-lg-none" v-if="isOpen">
        <ul class="Navbar-menu-list Navbar-menu-list--mobile">
          <li v-for="(link, i) in siteConfig.navigation" @click="updateAndClose(link, 'fade')" :key="i">
            <router-link :to="link.slug">{{ link.title }}</router-link>
          </li>
        </ul>
      </div>
    </transition>

    <div class="container">
      <div class="row">
        <!-- logo -->
        <div class="col-8 col-sm-6 col-lg-4">
          <router-link to="index">
            <img src="./../../../static/images/lww-logo.png" alt="">
          </router-link>
        </div>
        <!-- nav menu -->
        <div class="col-4 col-sm-6 col-lg-8 Navbar-menu">

          <!-- mobile menu toggle -->
          <div class="Navbar-menu-toggle d-lg-none" @click="toggleMenu()">
            <transition name="fade" mode="out-in">
              <icon name="bars" scale="1.5" v-if="!isOpen" :key="'open'"></icon>
              <icon name="close" scale="1.5" v-else :key="'close'"></icon>
            </transition>
          </div>

          <!-- desktop nav menu -->
          <div class="Navbar-menu Navbar-menu--desktop d-none d-lg-flex">
            <ul class="Navbar-menu-list Navbar-menu-list--desktop">
              <li 
              v-for="(link, i) in siteConfig.navigation" 
              :key="i"
              @click="updateState(link, 'fade')">
                <router-link :to="link.slug">{{ link.title }}</router-link>
              </li>
            </ul>
          </div>

        </div>
      </div>
    </div>
  </nav>
</template>

<script>
import siteConfig from './../../../static/data/site-config.json'
import Icon from 'vue-awesome/components/Icon'
export default {
  components: {
    Icon
  },

  data () {
    return {
      siteConfig,
      isOpen: false
    }
  },

  methods: {
    toggleMenu () {
      this.isOpen = !this.isOpen
    },

    updateAndClose (link, animation) {
      this.toggleMenu()
      this.updateState(link, animation)
    },

    updateState (link, animation) {
      let parent = link.defaultView.parent
      let view = link.defaultView.view
      let payload = {
        parent: parent,
        updates: {
          currentDetail: null,
          currentView: view,
          animation: animation || 'fade'
        }
      }
      if (parent && view !== '') {
        this.$store.dispatch('updateProps', payload)
      }
    }
  }
}
</script>


<style lang="scss" scoped>
@import "./../../assets/main.scss";
img {
  max-width: 100%;
}

.Navbar {
  align-items: center;
  background: #000;
  color: #f2f2f2;
  display: flex;
  position: fixed;
  height: $navBarHeight;
  z-index: 1000;
}

.Navbar-menu {
  align-items: center;
  display: flex;
  font-family: $fontSans;
  font-weight: bold;
  justify-content: flex-end;
  text-transform: uppercase;
}

.Navbar-menu--desktop {
  width: 100%;
}

.Navbar-menu--mobile {
  position: absolute;
  left: 0;
  top: 88px;
  width: 100vw;
  height: 100vh;
}

.Navbar-menu-list {
  align-items: center;
  display: flex;
  list-style-type: none;
  padding: 0;
  width: 100%;
}

.Navbar-menu-list--desktop {
  font-size: .85em;
  justify-content: space-between;
  margin: 0;
  height: 100%;
  a {
    transition: $transitionHover;
    &:hover {
      color: $gold;
    }
  }
}

.Navbar-menu-list--mobile {
  background: no-repeat center url('./../../../static/images/navbar-bg.jpg');
  background-size: cover;
  color: $offwhite;
  flex-direction: column;
  height: 100%;
  & li {
    background-color: $transparentGray;
    font-size: 1.5em;
    margin-top: 1em;
    padding: .5em 1em;
  }
}

.Navbar-menu-toggle {
  align-items: center;
  cursor: pointer;
  display: flex;
  justify-content: flex-end;
}

</style>




