<template>
  <div class="container">

    <div class="row">
      <div class="col" style="margin-top: 8em">
        <div class="ButtonIcon ButtonIcon--small ButtonIcon--light mx-auto" @click="updateState(null, 'appWolfmap', 'slideDown')">
          <icon name="arrow-up" scale=".85"></icon>
        </div>
      </div>
    </div>

    <transition :name="wolfState.animation" mode="out-in">
      <div class="row Detail" :key="Math.random()">

        <div class="col-1 Detail-button">
          <div class="ButtonIcon ButtonIcon--small ButtonIcon--light" 
          @click="updateState(prevDetail, 'appWolfDetail', 'slideRight')">
            <icon name="arrow-left" scale=".85"></icon>
          </div>
        </div>


        <div class="col-10">
          <img :src="'./static/images/' + wolfState.currentDetail.image" :alt="wolfState.currentDetail.title">
        </div>

        <div class="col-1 Detail-button">
          <div class="ButtonIcon ButtonIcon--small ButtonIcon--light" @click="updateState(nextDetail, 'appWolfDetail', 'slideLeft')">
            <icon name="arrow-right" scale=".85"></icon>
          </div>
        </div>

      </div>
    </transition>

    <transition :name="wolfState.animation" mode="out-in">
      <div class="row" :key="Math.random()">
        <div class="col-lg-6 Detail-desc">
          <h2>{{ wolfState.currentDetail.title }}</h2>
          <p>{{ wolfState.currentDetail.description }}</p>
        </div>
      </div>
    </transition>

  </div>
</template>


<script>
const _ = require('lodash')
import content from './../../../static/data/content.json'
import Icon from 'vue-awesome/components/Icon'
export default {
  components: {
    Icon
  },

  data () {
    return {
      content
    }
  },

  computed: {
    nextDetail () {
      let details = this.content.wolf.mapDetail
      let i = this.getDetailIndex(this.wolfState.currentDetail.name)
      if (i === details.length - 1) {
        return this.content.wolf.mapDetail[0]
      } else {
        i += 1
        return this.content.wolf.mapDetail[i]
      }
    },

    prevDetail () {
      let details = this.content.wolf.mapDetail
      let i = this.getDetailIndex(this.wolfState.currentDetail.name)
      if (i === 0) {
        i = details.length - 1
        return this.content.wolf.mapDetail[i]
      } else {
        i -= 1
        return this.content.wolf.mapDetail[i]
      }
    },

    wolfState () {
      return this.$store.getters.wolfState
    }
  },

  created () {
    window.addEventListener('keyup', this.handleKeyup)
  },

  beforeDestroy () {
    window.removeEventListener('keyup', this.handleKeyup)
  },

  methods: {
    getDetailIndex (detailName) {
      return _.findIndex(this.content.wolf.mapDetail, { name: detailName })
    },

    handleKeyup (e) {
      if (e.keyCode === 37) {
        this.updateState(this.prevDetail, 'appWolfDetail', 'slideRight')
      } else if (e.keyCode === 39) {
        this.updateState(this.nextDetail, 'appWolfDetail', 'slideLeft')
      } else if (e.keyCode === 38) {
        this.updateState(null, 'appWolfmap', 'slideDown')
      }
    },

    updateState (selection, view, animation) {
      let payload = {
        parent: 'wolfmap',
        updates: {
          currentDetail: selection,
          currentView: view,
          animation: animation || 'fade'
        }
      }
      this.$store.dispatch('updateProps', payload)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./../../assets/main.scss";

img {
  max-width: 100%;
}

.Detail {
  margin: 2em 0;
}

.Detail-button {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.Detail-desc {
  background: $transparentGray;
  color: $offwhite;
  font-family: $fontSerif;
  margin: 0 auto;
  padding: 2em;
}

</style>



