import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export const store = new Vuex.Store({
  state: {
    wolfmap: {
      currentDetail: null,
      currentView: 'appWolfmap',
      animation: 'fade'
    }
  },

  getters: {
    wolfState: state => state.wolfmap
  },

  mutations: {
    setProp (state, payload) {
      let parent = payload.parent
      let key = payload.key
      let value = payload.value

      state[parent][key] = value
    },

    updateProps (state, payload) {
      let parent = payload.parent
      let updates = payload.updates
      state[parent] = updates
    }
  },

  actions: {
    setProp ({ commit }, payload) {
      commit('setProp', payload)
    },

    updateProps ({ commit }, payload) {
      commit('updateProps', payload)
    }
  }
})



