<template>
  <div class="Audio">
    <audio id="ambient" :src="src" loop="true"></audio>
    <div class="Audio--controller ButtonIcon ButtonIcon--dark" @click="toggleAudio()">
      <transition name="fade" mode="out-in">
        <icon name="volume-up" v-if="isPlaying" :key="'on'"></icon>
        <icon name="volume-off" v-else :key="'off'"></icon>
      </transition>
    </div>
  </div>
</template>

<script>
import Icon from 'vue-awesome/components/Icon'
export default {
  components: {
    Icon
  },

  props: ['src'],

  data () {
    return {
      isPlaying: true
    }
  },

  computed: {
    audio () {
      return this.getAudio('ambient')
    }
  },

  methods: {
    getAudio (el) {
      return document.getElementById(el)
    },

    increaseVolume () {
      let vm = this
      let timer = setInterval(() => {
        if (vm.audio.volume < 0.9) {
          vm.audio.volume += 0.01
        } else {
          clearTimeout(timer)
        }
      }, 50)
    },

    initAudio () {
      this.audio.volume = 0
      this.audio.play()
      this.increaseVolume()
    },

    toggleAudio () {
      if (this.isPlaying) {
        this.audio.pause()
        this.isPlaying = false
      } else {
        this.audio.play()
        this.isPlaying = true
      }
    }
  },

  mounted () {
    // this.initAudio()
  }
}
</script>

<style lang="scss" scoped>
@import "./../../assets/main.scss";

.Audio--controller {
  position: fixed;
  bottom: 25px;
  right: 25px;
  z-index: 1000;
}
</style>




