<template>
  <div :style="{ overflow: overflow }" style="height: 100vh">
    <app-audio :src="'./static/audio/' + content.wolf.audio"></app-audio>
    <app-background :backgroundImg="'./static/images/' + content.wolf.backgroundImg"></app-background>

    <transition 
    :name="wolfState.animation" 
    mode="out-in" 
    v-on:enter="overflow = 'hidden'" 
    v-on:leave="overflow = 'auto'">
      <component :is="wolfState.currentView"></component>
    </transition>

  </div>
</template>



<script>
import appAudio from './../partials/audio'
import appBackground from './../partials/background'
import appWolfmap from './../explore/wolf-map'
import appWolfDetail from './../explore/wolf-detail'
import content from './../../../static/data/content.json'
export default {
  components: {
    appAudio,
    appBackground,
    appWolfmap,
    appWolfDetail
  },

  data () {
    return {
      content,
      overflow: 'auto'
    }
  },

  computed: {
    wolfState () {
      return this.$store.getters.wolfState
    }
  }
}
</script>

<style lang="scss" scoped>
@import "./../../assets/main.scss";

</style>



